.product_row{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 8rem;
}

.product_img{
    width: 40%;
}

.product_details{
    width: 60%;
}

.product_price{
    display: flex;
    padding-top: 2rem;
}

.newPrice{
    color: brown;
}

.oldPrice{
    text-decoration: line-through;
}

.discription{
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: left;
    padding-top: 0.9rem;
}

.addToCart{
    margin-top: 1.5rem;;
}

.add_to_cart{
    background-color: brown;
    color: white;
    text-decoration: none;
    padding: 0.5rem 3rem;
}

.add_to_cart:hover{
    background-color: rgb(255, 0, 0);
    color: white;
}

.category{
    display: flex;
    padding-top: 0.8rem;
}

.categoryHeading{
    font-weight: 600;
    padding-right: 10px;
}

@media only screen and (max-width: 992px){
    .product_row{
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
    }

    .product_img{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .product_details{
        width: 100%;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        padding-top: 2rem;
    }

    .product_price{
        display: flex;
        padding-top: 2rem;
        align-items: center;
        justify-content: center;
    }

    .category{
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 0.8rem;
    }
    
    .addToCart{
        margin-bottom: 2rem;;
    }
}