.login_form{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 8rem auto;
    background-color: black;
    padding: 2rem;
    border-radius: 10px;
    border: 1px solid white;
  }
  
  .login_form .input_type{
    padding-top: 0.7rem;
    margin-top: 1rem;
    border-top: none;
    border-left: none;
    border-right: none;
    background: transparent;
    border-bottom-color: darkorange;
    outline: none;
    color: white;
  }
  
  .login_here{
    background-color: white;
    color: darkorange;
    text-align: center;
    font-size: 1.7rem;
    padding: 0.4rem;
    border-radius: 2%;
  }
  
  .login_btn{
    background-color: rgb(240, 148, 36);
    color: black;
    margin-top: 1.3rem;
    padding: 0.5rem;
    outline: none;
    border: none;
    cursor: pointer;
  }
  
  .login_btn:hover{
    background-color: darkorange;
  }

  .already_login{
    color: darkorange;
    padding: 1rem;
    text-align: center;
  }