.offers{
    height: 60vh;
    display: flex;
    flex-wrap: wrap;
    margin: auto;
    padding: 0px 9rem;
    margin-bottom: 15rem;
    margin-top: 5rem;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
}

.offers_left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers_left h1{
    color: #171717;
    font-size: 3rem;
    font-weight: 600;
}

.offers_left button{
    width: 15rem;
    height: 3rem;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 1rem;
    font-weight: 500;
    margin-top: 1.8rem;
    cursor: pointer;
}

.offers_right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 3rem;
}

@media only screen and (max-width: 1199px){
    .offers_left h1{
        font-size: 2rem;
    }
}

@media only screen and (max-width: 992px){

    .offers_left h1,p{
        text-align: center;
        padding-top: 1rem;
    }

    .offers_left button{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .imge{
       padding: 2rem;
       margin: 1rem;
       height: 400px;
       width: 300px;
       text-align: center;
    }
}

@media only screen and (max-width: 536px){

    .imge{
       padding: 2rem;
       margin: -1rem;
    }
}

@media only screen and (max-width: 469px){

    .imge{
       padding: 4rem;
       margin: -3rem;
       height: 400px;
       width: 300px;
       text-align: center;
    }
}